h1,h2,h3,h4,h5,h6,p,span,button,li{
  font-family: "Inter", sans-serif;
  color: #18181B;
}
*{
 margin: 0;
 padding: 0; 
}
li{
  list-style: none;
}

.containerHeader{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 53px;
  opacity: 0.95;
  border-bottom: 1px solid #e4e4e7;
  position: fixed;
  top:0;
  background-color: #fff;
  z-index: 2;
}
.header {
  max-width: 1400px;
  width: 100%;
