  justify-content: space-between;
  padding: 16px 8px;
}
.IosMenu{
  display: none;
  cursor: pointer;
}
.header h1{
  font-size: 1.25rem;
}
.loginBlock h3{
  font-size: 14px;
  font-weight: 500;
  margin-right: 15px;
}
.firstsimvol{
  color: #EA3F40;
  margin-left: 10px;
  font-size: 1.25em;
}
.loginBlock{
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: end;
}
.loginBlock button{
  line-height: 1.25rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  background-color: #fff;
  border: 1px solid #e2e8f0;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;
}
.loginBlock button:hover{
  background-color: #f1f5f9
}

.containerMain{
  position: relative;
  /* height: 900px; */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.textBlock{
  max-width: 1400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}
.background{
  position: absolute;
  top:53px;
  overflow: hidden;
  opacity: .4;
  z-index: -1;
  width: 100%; /* Задаем ширину на 100% */
  height: 100%; /* Задаем высоту на 100% */
}
.background img{
  width: 100%;
}
.containerMain h1{
  font-size: min(4.5vw, 4.5rem);
  text-align: center;
  line-height: .9em;
  opacity: 0;
  animation: fade-in-up .3s ease-in .2s forwards;
}
@keyframes fade-in-up {
  from {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.animated-text{
  animation: textColorAnimation 8s linear infinite;
  
}
@keyframes textColorAnimation {
  0% {
    color: #4f46e5; /* Начальный цвет */
  }
  50% {
    color: #c084fc; /* Промежуточный цвет */
  }
  100% {
    color: #4f46e5; /* Конечный цвет */
  }
}

.containerMain h2{
  color: #71717a;
  font-size: min(5vw, 1.3rem);
  padding-top: 1em;
  padding-bottom: 1em;
  font-weight: 400;
  text-align: center;
  opacity: 0;
  animation: fade-in-up .3s ease-in .3s forwards;
}
.btnStack{
